// components/assets/assets-grid.tsx
'use client'

import { useState, useMemo } from 'react'
import { Card, CardContent, CardHeader } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table'
import { 
  MoreHorizontal, 
  FileText, 
  Image, 
  Video, 
  FileIcon,
  Download,
  Edit,
  Trash2,
  Eye
} from 'lucide-react'
import { formatDistanceToNow } from 'date-fns'
import { DeleteConfirmationDialog } from '@/components/dialogs/delete-confirmation-dialog'

interface Asset {
  id: string
  projectId: string
  type: 'image' | 'video' | 'pdf' | 'document'
  title: string
  addedBy: string
  updatedAt: string
  size?: number
}

interface AssetsGridProps {
  assets: Asset[]
  viewMode: 'grid' | 'list'
  searchTerm: string
  typeFilter: string
  projectFilter: string
  sortBy: string
  onAssetUpdate: (asset: Asset) => void
  onAssetDelete: (assetId: string) => void
}

// Get file type icon
function getFileTypeIcon(type: string) {
  switch (type) {
    case 'image':
      return <Image className="h-4 w-4" />
    case 'video':
      return <Video className="h-4 w-4" />
    case 'pdf':
      return <FileText className="h-4 w-4" />
    default:
      return <FileIcon className="h-4 w-4" />
  }
}

// Get badge color for file type
function getTypeBadgeColor(type: string) {
  switch (type) {
    case 'image':
      return 'bg-green-100 text-green-800'
    case 'video':
      return 'bg-blue-100 text-blue-800'
    case 'pdf':
      return 'bg-red-100 text-red-800'
    case 'document':
      return 'bg-purple-100 text-purple-800'
    default:
      return 'bg-gray-100 text-gray-800'
  }
}

// Asset Actions Component
function AssetActions({ 
  asset, 
  onUpdate, 
  onDelete 
}: { 
  asset: Asset
  onUpdate: (asset: Asset) => void
  onDelete: (assetId: string) => void
}) {
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false)

  const handleDelete = () => {
    onDelete(asset.id)
    setDeleteDialogOpen(false)
  }

  return (
    <>
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" size="icon" className="h-8 w-8">
            <MoreHorizontal className="h-4 w-4" />
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="end" className="z-50">
          <DropdownMenuItem>
            <Eye className="mr-2 h-4 w-4" />
            Preview
          </DropdownMenuItem>
          <DropdownMenuItem>
            <Download className="mr-2 h-4 w-4" />
            Download
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => onUpdate(asset)}>
            <Edit className="mr-2 h-4 w-4" />
            Edit
          </DropdownMenuItem>
          <DropdownMenuSeparator />
          <DropdownMenuItem 
            onClick={() => setDeleteDialogOpen(true)}
            className="text-red-600"
          >
            <Trash2 className="mr-2 h-4 w-4" />
            Delete
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>

      <DeleteConfirmationDialog
        open={deleteDialogOpen}
        onOpenChange={setDeleteDialogOpen}
        title="Delete Asset"
        description={`Are you sure you want to delete "${asset.title}"? This action cannot be undone.`}
        onConfirm={handleDelete}
      />
    </>
  )
}

// Grid View Component
function AssetGridView({ 
  assets, 
  onUpdate, 
  onDelete 
}: { 
  assets: Asset[]
  onUpdate: (asset: Asset) => void
  onDelete: (assetId: string) => void
}) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
      {assets.map((asset) => (
        <Card key={asset.id} className="group hover:shadow-md transition-shadow">
          <CardHeader className="p-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                {getFileTypeIcon(asset.type)}
                <Badge className={getTypeBadgeColor(asset.type)}>
                  {asset.type}
                </Badge>
              </div>
              <AssetActions 
                asset={asset} 
                onUpdate={onUpdate} 
                onDelete={onDelete} 
              />
            </div>
            
            <div className="space-y-2">
              <h3 className="font-medium text-sm truncate">{asset.title}</h3>
              
              <div className="flex items-center justify-between text-xs text-gray-500">
                <span>Added by {asset.addedBy}</span>
                <span>{formatDistanceToNow(new Date(asset.updatedAt), { addSuffix: true })}</span>
              </div>
            </div>
          </CardHeader>
        </Card>
      ))}
    </div>
  )
}

// List View Component
function AssetListView({ 
  assets, 
  onUpdate, 
  onDelete 
}: { 
  assets: Asset[]
  onUpdate: (asset: Asset) => void
  onDelete: (assetId: string) => void
}) {
  return (
    <div className="border rounded-lg">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Name</TableHead>
            <TableHead>Type</TableHead>
            <TableHead>Added By</TableHead>
            <TableHead>Updated</TableHead>
            <TableHead className="w-10"></TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {assets.map((asset) => (
            <TableRow key={asset.id}>
              <TableCell>
                <div className="flex items-center space-x-2">
                  {getFileTypeIcon(asset.type)}
                  <span className="font-medium">{asset.title}</span>
                </div>
              </TableCell>
              <TableCell>
                <Badge className={getTypeBadgeColor(asset.type)}>
                  {asset.type}
                </Badge>
              </TableCell>
              <TableCell>
                <div className="flex items-center space-x-2">
                  <Avatar className="h-6 w-6">
                    <AvatarFallback className="text-xs">
                      {asset.addedBy.charAt(0).toUpperCase()}
                    </AvatarFallback>
                  </Avatar>
                  <span className="text-sm">{asset.addedBy}</span>
                </div>
              </TableCell>
              <TableCell className="text-sm text-gray-500">
                {formatDistanceToNow(new Date(asset.updatedAt), { addSuffix: true })}
              </TableCell>
              <TableCell>
                <AssetActions 
                  asset={asset} 
                  onUpdate={onUpdate} 
                  onDelete={onDelete} 
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  )
}

// Main Assets Grid Component
export function AssetsGrid({
  assets,
  viewMode,
  searchTerm,
  typeFilter,
  projectFilter,
  sortBy,
  onAssetUpdate,
  onAssetDelete
}: AssetsGridProps) {
  // Internal filtering and sorting logic
  const filteredAndSortedAssets = useMemo(() => {
    let filtered = assets.filter(asset => {
      const matchesSearch = asset.title.toLowerCase().includes(searchTerm.toLowerCase())
      const matchesType = typeFilter === 'all' || asset.type === typeFilter
      const matchesProject = projectFilter === 'all' || asset.projectId === projectFilter
      return matchesSearch && matchesType && matchesProject
    })

    // Sort assets
    switch (sortBy) {
      case 'newest':
        filtered.sort((a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())
        break
      case 'oldest':
        filtered.sort((a, b) => new Date(a.updatedAt).getTime() - new Date(b.updatedAt).getTime())
        break
      case 'alphabetical':
        filtered.sort((a, b) => a.title.localeCompare(b.title))
        break
      case 'size':
        // Sort by file size if available, otherwise by title
        filtered.sort((a, b) => {
          const sizeA = a.size || 0
          const sizeB = b.size || 0
          return sizeB - sizeA
        })
        break
      default:
        break
    }

    return filtered
  }, [assets, searchTerm, typeFilter, projectFilter, sortBy])

  if (filteredAndSortedAssets.length === 0) {
    return (
      <Card>
        <CardContent className="flex flex-col items-center justify-center py-12">
          <FileIcon className="h-12 w-12 text-gray-400 mb-4" />
          <h3 className="text-lg font-medium text-gray-900 mb-2">
            No assets found
          </h3>
          <p className="text-gray-500 text-center">
            {assets.length === 0 
              ? "Upload your first asset to get started"
              : searchTerm || typeFilter !== 'all' || projectFilter !== 'all'
                ? "No assets match your current filters"
                : "No assets available"
            }
          </p>
        </CardContent>
      </Card>
    )
  }

  return viewMode === 'grid' ? (
    <AssetGridView 
      assets={filteredAndSortedAssets} 
      onUpdate={onAssetUpdate} 
      onDelete={onAssetDelete} 
    />
  ) : (
    <AssetListView 
      assets={filteredAndSortedAssets} 
      onUpdate={onAssetUpdate} 
      onDelete={onAssetDelete} 
    />
  )
}